.table {
	display: table;
	border-collapse: separate;
	box-sizing: border-box;
	text-indent: initial;
	border-spacing: 2px;
	border-color: grey;
	width: 80%;
	margin: 0;
	outline: 1px solid black;
}

.table__row {
	display: table-row;
	vertical-align: inherit;
	border-color: inherit;
}

.table__row:nth-of-type(odd) {
	background-color: rgba(0, 0, 0, .05);
}

.table__hight {
	padding: .75rem;
	vertical-align: top;
	border-top: 1px solid #dee2e6;
	text-align: center;
	vertical-align: bottom;
	border-bottom: 2px solid #dee2e6;
}
.table__hight_active {
	cursor: pointer;
	transition: 0.3s ease;
}

.table__hight_active:hover {
	opacity: 0.6;
}

.table__down {
	padding: .75rem;
	vertical-align: top;
	border-top: 1px solid #dee2e6;
	text-align: center;
}
